import React from "react";
import styled from "styled-components";

const Main = styled.div`
  width: 100%;
  height: 70vh;
  margin: 10vh 0;
  display: flex;
  align-itens: center;
  justify-content: center;
`;

const PhotoDiv = styled.div`
  width: 30%;
  height: 80%;
  background: aqua;
`;

export default function index() {
    return (
        <Main>
          <PhotoDiv></PhotoDiv>
        </Main>
    );
}